@import url('https://fonts.googleapis.com/css?family=Quicksand&display=swap');
body {
  font-family: "Quicksand", sans-serif;
  margin: 0;  
}

.form {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.invalid > input {
  border: 2px solid red;
}
.invalid {
  color: red;
}

